import React, { useState, useEffect } from "react";

export default () => {
  const [time, setTime] = useState(0);
  useEffect(() => {
    let k = setInterval(() => {
      setTime(new Date().toLocaleTimeString().replace("AM","").replace("PM",""));
    }, 1000);
    return () => window.clearInterval(k);
  });
  return <>{time}</>;
};
